<template>
  <div class="divMax" ref="eleDiv">
    <span class="sp">重力加速度：</span>
    <input type="number" step="0.1" min=0 v-model="ACCELERATION_G">
    <span class="sp">反射系数：</span>
    <input type="number" step="0.01" min=0 max=1 v-model="REFLECTANCE">
    <br>
    <span class="sp">存留时间：</span>
    <input type="number" :step="LOOPINTERVAL" min="0" v-model="KEEPTIME">
    <span class="sp">速度倍数：</span>
    <input type="range" step="1" v-bind:title="VELOCITY_Multiple" min=1 max=20 v-model="VELOCITY_Multiple">
    <span>{{VELOCITY_Multiple}}</span>
    <br>
    <canvas class="cv" v-bind:width="CANVAS.WIDTH" v-bind:height="CANVAS.HEIGHT" @mousedown="$_NewLineStart" @mousemove="$_NewLineDraw" @mouseup="$_NewLineEnd" @mouseout="$_NewLineEnd" ref="eleCv"></canvas>
  </div>
</template>
<script>
var ctx
var div
var timer
export default {
  name: 'Missile',
  data() {
    return {
      CANVAS: { WIDTH: 500, HEIGHT: 500 },
      ACCELERATION_G: 5, //重力加速度
      REFLECTANCE: 0.8, //反射系数,
      KEEPTIME: 0, //静止后的留存时间(ms),0表示无限
      LOOPINTERVAL: 30, //帧间隔(ms),
      VELOCITY_Multiple: 1, //速度倍数
      CIRCLE_PARAMETER: { RADIUS: 5, FILL_COLOR: '#FFA500' }, //圆点参数
      RESISTANCE_COEFFICIENT: { x: 0.009, y: 0.001 }, //阻力系数
      curLine: { start: { x: 0, y: 0 }, end: { x: 0, y: 0 }, status: false },
      arrMissile: []
    }
  },
  mounted() {
    var self = this
    ctx = self.$refs.eleCv.getContext('2d')
    div = self.$refs.eleDiv
    self.$_DrawMissiles()
    self.$_SetInterval(30)
  },
  methods: {
    $_NewLineStart: function() {
      var self = this
      let e = event || window.event
      let rect = self.$refs.eleCv.getBoundingClientRect()
      let [x, y] = [
        e.offsetX / rect.width * self.CANVAS.WIDTH,
        e.offsetY / rect.height * self.CANVAS.HEIGHT
      ]
      self.curLine = {
        start: { x: x, y: y },
        end: { x: x, y: y },
        status: true
      }
    },
    $_NewLineDraw: function() {
      var self = this
      if (self.curLine.status) {
        let e = event || window.event
        let rect = self.$refs.eleCv.getBoundingClientRect()
        let [x, y] = [
          e.offsetX / rect.width * self.CANVAS.WIDTH,
          e.offsetY / rect.height * self.CANVAS.HEIGHT
        ]
        self.curLine.end = { x: x, y: y }
        self.$_ClearWhole()
        self.$_DrawMissiles()
        self.$_DrawLine()
      }
    },
    $_NewLineEnd: function() {
      var self = this
      if (self.curLine.status) {
        //Create New Point
        let [distanceX, distanceY] = [
          Math.floor(self.curLine.start.x - self.curLine.end.x),
          Math.floor(self.curLine.start.y - self.curLine.end.y)
        ]
        self.arrMissile.push({
          point: { x: self.curLine.end.x, y: self.curLine.end.y },
          acceleration: { x: 0, y: 0 },
          velocity: { x: distanceX, y: distanceY },
          color:
            'RGB(' +
            Math.floor(Math.random() * 255) +
            ',' +
            Math.floor(Math.random() * 255) +
            ',' +
            Math.floor(Math.random() * 255) +
            ')',
          deadTime: 0
        })

        //Clear Line
        self.curLine = {
          start: { x: 0, y: 0 },
          end: { x: 0, y: 0 },
          status: false
        }
        self.$_ClearWhole()
        self.$_DrawMissiles()
      }
    },
    $_DrawMissiles: function() {
      var self = this
      self.arrMissile.forEach(item => {
        ctx.strokeStyle = '#FFF'
        ctx.beginPath()
        ctx.arc(
          item.point.x,
          item.point.y,
          self.CIRCLE_PARAMETER.RADIUS,
          0,
          Math.PI * 2,
          true
        )
        ctx.fill()
        ctx.fillStyle = item.color
        ctx.arc(
          item.point.x,
          item.point.y,
          self.CIRCLE_PARAMETER.RADIUS - 1,
          0,
          Math.PI * 2,
          true
        )
        ctx.fill()
      })
    },
    $_DrawLine: function() {
      var self = this
      ctx.strokeStyle = '#FFA500'
      ctx.beginPath()
      ctx.moveTo(self.curLine.start.x, self.curLine.start.y)
      ctx.lineTo(self.curLine.end.x, self.curLine.end.y)
      ctx.stroke()
    },
    $_ClearWhole: function() {
      var self = this
      ctx.clearRect(0, 0, self.CANVAS.WIDTH, self.CANVAS.HEIGHT)
    },
    $_Calculate: function() {
      var self = this
      let [minWidth, maxWidth, minHeight, maxHeight] = [
        self.CIRCLE_PARAMETER.RADIUS,
        self.CANVAS.WIDTH - self.CIRCLE_PARAMETER.RADIUS,
        self.CIRCLE_PARAMETER.RADIUS,
        self.CANVAS.HEIGHT - self.CIRCLE_PARAMETER.RADIUS
      ]
      if (self.KEEPTIME != 0) {
        self.arrMissile = self.arrMissile.filter(value => {
          return value.deadTime < self.KEEPTIME
        })
      }

      self.arrMissile.forEach(item => {
        //水平方向处理
        item.point.x =
          item.point.x +
          item.velocity.x * (self.LOOPINTERVAL / 1000) * self.VELOCITY_Multiple
        if (item.point.x > maxWidth) {
          item.velocity.x = -item.velocity.x * self.REFLECTANCE
          item.point.x =
            maxWidth -
            (item.point.x - maxWidth) * (1 - self.RESISTANCE_COEFFICIENT.x)
        } else if (item.point.x < minWidth) {
          item.velocity.x = -item.velocity.x * self.REFLECTANCE
          item.point.x =
            (minWidth + (minWidth - item.point.x)) *
            (1 - self.RESISTANCE_COEFFICIENT.x)
        }
        item.velocity.x = item.velocity.x * (1 - self.RESISTANCE_COEFFICIENT.x)
        item.velocity.x = self.$_NearByZero(item.velocity.x)

        //垂直方向处理
        item.point.y =
          item.point.y +
          item.velocity.y * (self.LOOPINTERVAL / 1000) * self.VELOCITY_Multiple
        if (item.point.y > maxHeight) {
          item.velocity.y = -item.velocity.y * self.REFLECTANCE
          item.point.y =
            maxHeight -
            (item.point.y - maxHeight) * (1 - self.RESISTANCE_COEFFICIENT.y)
          item.velocity.y =
            item.velocity.y * (1 - self.RESISTANCE_COEFFICIENT.y)
          item.velocity.y += self.ACCELERATION_G
          if (item.velocity.y >= 0) {
            //垂直方向停在'地表'
            item.point.y = maxHeight
            item.velocity.y = 0
            if (item.velocity.x == 0) {
              item.deadTime += self.LOOPINTERVAL
            }
          } else {
            item.velocity.y = self.$_NearByZero(item.velocity.y)
          }
        } else {
          if (item.point.y < minHeight) {
            item.velocity.y = -item.velocity.y * self.REFLECTANCE
            item.point.y =
              (minHeight + (minHeight - item.point.y)) *
              (1 - self.RESISTANCE_COEFFICIENT.y)
          }
          item.velocity.y =
            item.velocity.y * (1 - self.RESISTANCE_COEFFICIENT.y)
          item.velocity.y += self.ACCELERATION_G
          item.velocity.y = self.$_NearByZero(item.velocity.y)
        }
      })
    },
    $_NearByZero: function(value) {
      if (value > -0.1 && value < 0.1) {
        return 0
      } else {
        return value
      }
    },
    $_SetInterval: function(value) {
      var self = this
      clearInterval(timer)
      if (value == undefined) value = parseInt(self.$refs.eleInterval.value)
      self.LOOPINTERVAL = value
      timer = setInterval(function() {
        self.ACCELERATION_G = parseFloat(self.ACCELERATION_G)
        self.REFLECTANCE = parseFloat(self.REFLECTANCE)
        self.KEEPTIME = parseInt(self.KEEPTIME)
        self.VELOCITY_Multiple = parseInt(self.VELOCITY_Multiple)
        self.$_Calculate()
        self.$_ClearWhole()
        self.$_DrawMissiles()
        if (self.curLine.status) {
          self.$_DrawLine()
        }
      }, self.LOOPINTERVAL)
    }
  }
}
</script>
<style scoped>
.divMax {
  padding: 10px 15px 20px 45px;
}
.cv {
  margin-top:10px;
  width: 80vw;
  max-width: 80vh;
  height: 80vw;
  max-height: 80vh;
  background-color: black;
  cursor: crosshair;
}
.sp {
  display: inline-block;
  width: 110px;
  text-align: right;
}
.divMax input{
    vertical-align: middle;
}
</style>



