<template>
  <div>
    <canvas v-bind:width="cvWidth" v-bind:height="cvHeight" ref="eleCv"></canvas>
  </div>
</template>
<script>
export default {
  name: 'ChineseChess',
  data() {
    return {
      cvWidth: 500,
      cvHeight: 500,
      perLineWidth: 50,
      startX: 0,
      startY: 0,
      chessBoard: []
    }
  },
  mounted() {
    var self = this
    self.init(true)
    window.onresize = function() {
      self.init(false)
    }
  },
  methods: {
    init: function(render) {
      var self = this
      self.cvWidth = window.innerWidth
      self.cvHeight = window.innerHeight

      let width = self.cvWidth / 10
      let height = self.cvHeight / 11
      if (width >= height) {
        self.startY = height
        self.perLineWidth = height
        self.startX = (self.cvWidth - self.perLineWidth * 9) / 2
      } else {
        self.startX = width
        self.perLineWidth = width
        self.startY = (self.cvHeight - self.perLineWidth * 10) / 2
      }

      self.$nextTick(function() {
        if (render) self.render()
      })
    },
    chessBoardRender: function() {
      var self = this
      let ctx = self.$refs.eleCv.getContext('2d')
      ctx.strokeStyle = '#000'
      ctx.lineWidth = 0.1
      // -
      for (let i = 0; i < 11; i++) {
        ctx.moveTo(self.startX, self.startY + self.perLineWidth * i)
        ctx.lineTo(
          self.startX + self.perLineWidth * 8,
          self.startY + self.perLineWidth * i
        )
        //ctx.stroke()
      }
      // |
      for (let i = 0; i < 9; i++) {
        if (i == 0 || i == 8) {
          ctx.moveTo(self.startX + self.perLineWidth * i, self.startY)
          ctx.lineTo(
            self.startX + self.perLineWidth * i,
            self.startY + self.perLineWidth * 9
          )
          //ctx.stroke()
        } else {
          ctx.moveTo(self.startX + self.perLineWidth * i, self.startY)
          ctx.lineTo(
            self.startX + self.perLineWidth * i,
            self.startY + self.perLineWidth * 4
          )
          //ctx.stroke()
          ctx.moveTo(
            self.startX + self.perLineWidth * i,
            self.startY + self.perLineWidth * 5
          )
          ctx.lineTo(
            self.startX + self.perLineWidth * i,
            self.startY + self.perLineWidth * 9
          )
          //ctx.stroke()
        }
      }
      // *
      ctx.moveTo(
        self.startX + self.perLineWidth * 3,
        self.startY + self.perLineWidth * 9
      )
      ctx.lineTo(
        self.startX + self.perLineWidth * 5,
        self.startY + self.perLineWidth * 7
      )
      ctx.moveTo(
        self.startX + self.perLineWidth * 3,
        self.startY + self.perLineWidth * 7
      )
      ctx.lineTo(
        self.startX + self.perLineWidth * 5,
        self.startY + self.perLineWidth * 9
      )
      ctx.moveTo(
        self.startX + self.perLineWidth * 3,
        self.startY + self.perLineWidth * 0
      )
      ctx.lineTo(
        self.startX + self.perLineWidth * 5,
        self.startY + self.perLineWidth * 2
      )
      ctx.moveTo(
        self.startX + self.perLineWidth * 3,
        self.startY + self.perLineWidth * 2
      )
      ctx.lineTo(
        self.startX + self.perLineWidth * 5,
        self.startY + self.perLineWidth * 0
      )

      //+
      function markRender(x, y, bLeft, bRight) {
        let spaceWidth = self.perLineWidth * 1 / 16
        if (bLeft) {
          ctx.moveTo(x - spaceWidth, y - spaceWidth)
          ctx.lineTo(x - spaceWidth, y - spaceWidth - self.perLineWidth * 1 / 5)
          ctx.moveTo(x - spaceWidth, y - spaceWidth)
          ctx.lineTo(x - spaceWidth - self.perLineWidth * 1 / 5, y - spaceWidth)

          ctx.moveTo(x - spaceWidth, y + spaceWidth)
          ctx.lineTo(x - spaceWidth, y + spaceWidth + self.perLineWidth * 1 / 5)
          ctx.moveTo(x - spaceWidth, y + spaceWidth)
          ctx.lineTo(x - spaceWidth - self.perLineWidth * 1 / 5, y + spaceWidth)
        }
        if (bRight) {
          ctx.moveTo(x + spaceWidth, y - spaceWidth)
          ctx.lineTo(x + spaceWidth, y - spaceWidth - self.perLineWidth * 1 / 5)
          ctx.moveTo(x + spaceWidth, y - spaceWidth)
          ctx.lineTo(x + spaceWidth + self.perLineWidth * 1 / 5, y - spaceWidth)

          ctx.moveTo(x + spaceWidth, y + spaceWidth)
          ctx.lineTo(x + spaceWidth, y + spaceWidth + self.perLineWidth * 1 / 5)
          ctx.moveTo(x + spaceWidth, y + spaceWidth)
          ctx.lineTo(x + spaceWidth + self.perLineWidth * 1 / 5, y + spaceWidth)
        }
      }
      for(let i=0;i<5;i++){
        markRender(self.startX+self.perLineWidth*2*i,self.startY+self.perLineWidth*3,i>0,i<4)
        markRender(self.startX+self.perLineWidth*2*i,self.startY+self.perLineWidth*6,i>0,i<4)
      }
      markRender(
        self.startX + self.perLineWidth * 1,
        self.startY + self.perLineWidth * 2,
        true,
        true
      )
      markRender(
        self.startX + self.perLineWidth * 1,
        self.startY + self.perLineWidth * 7,
        true,
        true
      )
      markRender(
        self.startX + self.perLineWidth * 7,
        self.startY + self.perLineWidth * 2,
        true,
        true
      )
      markRender(
        self.startX + self.perLineWidth * 7,
        self.startY + self.perLineWidth * 7,
        true,
        true
      )

      ctx.stroke()
    },
    chessManRender:function(){
      var self=this
      let ctx = self.$refs.eleCv.getContext('2d')
      ctx.fillStyle='#FFF'
      self.chessBoard.forEach(function(boardLine,iY){
        boardLine.forEach(function(item,iX){
          if(item){
            ctx.strokeStyle=item.team==1?'rgb(255,0,0)':'rgb(0,0,0)'
            ctx.beginPath()
            ctx.arc(self.startX+self.perLineWidth*iX,self.startY+self.perLineWidth*iY,self.perLineWidth*1/3,0,Math.PI*2,true)
            ctx.closePath()
            ctx.fill()
            ctx.stroke()
          }
        })
      })
      ctx.moveTo(0,0)
      //ctx.stroke()
    },
    render: function() {
      var self = this
      self.cvRender()
      self.chessBoardRender()
      self.chessBoard=[[{text:'车',team:0},{text:'马',team:0},{text:'象',team:0},{text:'士',team:0},{text:'将',team:0},{text:'士',team:0},{text:'象',team:0},{text:'马',team:0},{text:'车',team:0}],
                        [null,null,null,null,null,null,null,null,null],
                        [null,{text:'炮',team:0},null,null,null,null,null,{text:'炮',team:0},null],
                        [{text:'兵',team:0},null,{text:'兵',team:0},null,{text:'兵',team:0},null,{text:'兵',team:0},null,{text:'兵',team:0}],
                        [null,null,null,null,null,null,null,null,null],
                        [null,null,null,null,null,null,null,null,null],
                        [{text:'兵',team:1},null,{text:'兵',team:1},null,{text:'兵',team:1},null,{text:'兵',team:1},null,{text:'兵',team:1}],
                        [null,{text:'炮',team:1},null,null,null,null,null,{text:'炮',team:1},null],
                        [null,null,null,null,null,null,null,null,null],
                        [{text:'车',team:1},{text:'马',team:1},{text:'象',team:1},{text:'士',team:1},{text:'将',team:1},{text:'士',team:1},{text:'象',team:1},{text:'马',team:1},{text:'车',team:1}]
                      ]
      self.chessManRender()
      requestAnimationFrame(self.render)
    },
    cvRender: function() {
      var self = this
      let ctx = self.$refs.eleCv.getContext('2d')
      ctx.fillStyle = '#FFF'
      ctx.fillRect(0, 0, self.cvWidth, self.cvHeight)
    }
  }
}
</script>
<style scoped>

</style>



