import Vue from 'vue';
import Router from 'vue-router';
import Card from '@/components/Card';
import QRCode from '@/components/QRCode';
import ImageProcess from '@/components/ImageProcess';
import Map from '@/components/Map';
import Index from '@/components/Index'
import Welcome from '@/components/Welcome'
import Sudoku from '@/components/Games/Sudoku';
import Missile from '@/components/Games/Missile';
import Game1 from '@/components/Games/Game1';
import ChineseChess from '@/components/Games/ChineseChess';
import Default from '@/components/Default';

Vue.use(Router);
const routes = [
    {
        path:'/C',
        component:Index,
        children:[
            { path: '/', name: 'Index', component: Index },
            { path: '/Welcome', name: 'Welcome', component: Welcome },
            { path: '/ImageProcess', name: 'ImageProcess', component: ImageProcess },
            { path: '/QRCode', name: 'QRCode', component: QRCode },
            { path: '/Card', name: 'Card', component: Card },
            { path: '/Map', name: 'Map', component: Map },
            { path: '/Sudoku', name: 'Sudoku', component: Sudoku },
            { path: '/Missile', name: 'Missile', component: Missile }
        ]
    },
    {
        path:'/Game1',
        component:Game1
    },
    {
        path:'/ChineseChess',
        component:ChineseChess
    },
    {
        path:'/',
        name:'Default',
        component:Default
    }
    
];

const router = new Router({ 
    mode: 'history',
    routes: routes 
});

export default router;



