<template>
  <div>
    <canvas v-bind:width="cvWidth" v-bind:height="cvHeight" ref="eleCv"></canvas>
  </div>
</template>
<script>
export default {
  name: 'Game1',
  data() {
    return {
      cvWidth: 500,
      cvHeight: 500,
      ballList: [],
      raf: null
    }
  },
  mounted() {
    var self = this
    self.init(true)
    window.onresize = function() {
      self.init(false)
    }
  },
  methods: {
    init: function(render) {
      var self = this
      self.cvWidth = window.innerWidth
      self.cvHeight = window.innerHeight
      self.$nextTick(function() {
        self.ballGene(25)
        if (render) self.render()
      })
    },
    render: function() {
      var self = this
      self.cvRender()
      self.ballRender()
      requestAnimationFrame(self.render)
    },
    cvRender: function() {
      var self = this
      let ctx = self.$refs.eleCv.getContext('2d')
      ctx.fillStyle = '#000'
      ctx.fillRect(0, 0, self.cvWidth, self.cvHeight)
    },
    ballRender: function() {
      var self = this
      let ctx = self.$refs.eleCv.getContext('2d')
      let len = self.ballList.length

      for (let i = 0; i < len; i++) {
        //draw
        ctx.beginPath()
        ctx.strokeStyle = 'rgba(255,255,255,0.5)'
        ctx.fillStyle =
          'rgba(' +
          self.ballList[i].color.r +
          ',' +
          self.ballList[i].color.g +
          ',' +
          self.ballList[i].color.b +
          ',' +
          self.ballList[i].color.a +
          ')'
        ctx.arc(
          self.ballList[i].poi.x,
          self.ballList[i].poi.y,
          self.ballList[i].r,
          0,
          Math.PI * 2,
          true
        )
        ctx.fill()
        ctx.stroke()
        //move
        self.ballList[i].poi.x += self.ballList[i].v.x / 60
        self.ballList[i].poi.y += self.ballList[i].v.y / 60
        //check 'x'
        if (self.ballList[i].poi.x - self.ballList[i].r < 0) {
          self.ballList[i].poi.x =
            self.ballList[i].r + (self.ballList[i].r - self.ballList[i].poi.x)
          self.ballList[i].v.x = -self.ballList[i].v.x
        } else if (self.ballList[i].poi.x + self.ballList[i].r > self.cvWidth) {
          self.ballList[i].poi.x =
            self.cvWidth -
            self.ballList[i].r -
            (self.ballList[i].poi.x + self.ballList[i].r - self.cvWidth)
          self.ballList[i].v.x = -self.ballList[i].v.x
        }
        //check 'y'
        if (self.ballList[i].poi.y - self.ballList[i].r < 0) {
          self.ballList[i].poi.y =
            self.ballList[i].r + (self.ballList[i].r - self.ballList[i].poi.y)
          self.ballList[i].v.y = -self.ballList[i].v.y
        } else if (
          self.ballList[i].poi.y + self.ballList[i].r >
          self.cvHeight
        ) {
          self.ballList[i].poi.y =
            self.cvHeight -
            self.ballList[i].r -
            (self.ballList[i].poi.y + self.ballList[i].r - self.cvHeight)
          self.ballList[i].v.y = -self.ballList[i].v.y
        }
      }
    },
    random: function(minValue, maxValue) {
      return minValue + Math.floor(Math.random() * (maxValue - minValue))
    },
    ballGene: function(n) {
      var self = this
      self.ballList.length = 0
      for (let i = 0; i < n; i++) {
        self.ballList.push({
          poi: {
            x: self.random(0, self.cvWidth),
            y: self.random(0, self.cvHeight)
          },
          r: self.random(3, 5),
          color: {
            r: self.random(0, 255),
            g: self.random(0, 255),
            b: self.random(0, 255),
            a: 1
          },
          v: { x: self.random(60, 80), y: self.random(60, 80) }
        })
      }
    }
  }
}
</script>
<style scoped>

</style>



