<template>
  <span class="title">{{text}}</span>
</template>
<script>
export default {
    name:'SmallTitle',
    props:{
        text:String
    }
}
</script>
<style scoped>
    .title{
        position: absolute;
        top:5px;
        left:50%;
        transform: translateX(-50%);
        color:blueviolet;
    }
</style>



